<template lang='pug'>
v-app
  v-container(fluid class='top')
    v-layout(fluid text-xs-center align-center justify-center row wrap)
      v-flex(id='top' xs12 sm12 md10 lg10)
        br
        img(src='./assets/logo.png' id='logo')
        br
        br
        h1 The Marine Biological Association 
        h1 16th Annual Postgraduate Conference
        h2 5th - 8th May 2019 &nbsp; &nbsp; · &nbsp; &nbsp; Newcastle University
        br
        v-btn(href='mailto:mba2019@newcastle.ac.uk' large color="base" dark) Register your interest
        br
  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex( xs11 sm10 md10 lg10)
        //- h1 About
        br
        h1(class='text-xs-center') The Marine Biological Association’s 16th Annual Postgraduate Conference is taking place in May 2019 at Newcastle University. 
        br
        p The 16th Annual MBA Postgraduate Conference 2019 aims to bring together postgraduate students, newly graduated students, research assistants and post-doctoral research fellows to promote their research, whilst providing an opportunity to meet with senior academics and leaders in marine and maritime industries. With a touch of the famous northern hospitality at Newcastle University, we aim to create a diverse, inclusive and thought-provoking environment for budding marine scientists, to stimulate discussion about cutting edge research and generate ideas that may lead to future collaborations. By liaising with local businesses and industry leaders in the marine sector, we hope to encourage those who attend to think about future connections between industry science partners. 
        p At this conference we are committed to sustainability, therefore we are going paperless! Daily schedules are available to be downloaded below and keep up to date with all conference details on our social media pages. Our merchandise is eco-friendly and only reusable alternatives to single use plastics will be available. Wherever possible, we’ll be donating leftover food to local food banks.
        br
      
  v-container(fluid class="base")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10 class='text-xs-center')
        br
        img( src='./assets/MBAw.png' id='logo')
        h1(class='text-xs-center' id='social') #MBA19
        h2(class='text-xs-center') Connect with us:
        v-container(text-xs-center)
          v-layout
            v-flex(xs12 lg3)
              a(href='mailto:mba2019@newcastle.ac.uk')
                i(class='fas fa-envelope fa-4x' id='icon')
                
            v-flex(xs12 lg3)
              a(href='https://www.facebook.com/mbaconference19/')
                i(class='fab fa-facebook fa-4x'  id='icon')

            v-flex(xs12 lg3)
              a(href='https://www.twitter.com/mbaconference19/')
                i(class='fab fa-twitter fa-4x' id='icon')

            v-flex(xs12 lg3)
              a(href='https://www.instagram.com/mbaconference19/')
                i(class='fab fa-instagram fa-4x' id='icon')

  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Full program
        br
        p The conference will begin officially at <strong>8:30am on Monday 6th May</strong>. Please click on a day to see the full program of that day's events.
        br
        h2(class='text-xs-center')
          a(href='https://www.scribd.com/document/395310325/MBA-Sunday' target="_blank") Sunday 5th May
        br
        p The 5th May is a cracking opportunity for a day tour of the Northumberland Coast alongside a Northumberland Inshore Fisheries & Conservation Authority representative! 40 places are available on the coach, allowing delegates to see the beauty that the North East has to offer! We’ll be taking you to the <strong>seaside town of Amble </strong> for a tour of Amble Lobster Hatchery, followed by a stop at a traditional chip shop. Then we’re all off to <strong>St Mary’s Lighthouse</strong>, a Grade II listed building and heritage site in Whitley Bay, and we'll end the day at Newcastle University’s famous <strong>Dove Marine Laboratory</strong>. The evening will feature a presentation by our NIFCA representative, canapés and drinks, and a marine themed pub quiz! Afterwards the coach will take us back to the city. It’ll be a great way to get comfortable in the North East and meet the team and other delegates! Be sure to sign up when you register!
        br
        v-container(text-xs-center)
          v-layout
            v-flex(xs12 lg4)
              h2 
                a(href='https://www.scribd.com/document/395441980/MBA-Monday' target="_blank") Monday 6th May
            v-flex(xs12 lg4)
              h2 
                a(href='https://www.scribd.com/document/395441981/MBA-Tuesday' target="_blank") Tuesday 7th May
            v-flex(xs12 lg4)
              h2 
                a(href='https://www.scribd.com/document/395441982/MBA-Wednesday' target="_blank") Wednesday 8th May
  v-container(fluid class="dark")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Ticket information
        br
        p Non-member &nbsp; &nbsp; · &nbsp; &nbsp; £90
        p MBA member &nbsp; &nbsp; · &nbsp; &nbsp; £81.50
        p Early bird &nbsp; &nbsp; · &nbsp; &nbsp; £80
        p Early bird MBA member  &nbsp; &nbsp; · &nbsp; &nbsp; £72.50
        p MBA Student Ambassador  &nbsp; &nbsp; · &nbsp; &nbsp; £48
        p Early bird rates available until 31st January 2019.
        p All attending delegates will receive a free one year membership to the Marine Conservation Society.  
  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Keynotes
        br
        h2 Matt Frost <em>(MBA Director)</em>
        p Details to follow.
        h2 Professor Nick Polunin <em>(Newcastle University)</em>
        div
          //- img(src='./assets/nick.jpg' style="vertical-align:middle" id='logo')
          p A Marine Conservation Ecologist researching and lecturing ecosystem connectivity at Newcastle University, including reef fish-habitats and sustainable fisheries. Nick is also an editor for the Cambridge University Press and hugely involved with the Northumberland Inshore Fisheries and Conservation Authority (NIFCA).
        //- h2 Dr Amy Lusher <em>(Norwegian Institute for Water Research)</em>
        //- p A researcher in the field of marine pollution at the Norwegian Institute of Water Research, focusing on the distribution, interactions and effects of microplastics in the marine environment, and developing microplastic sampling methods from water, sediment and biota.
        h2 Professor Claire Hellio <em>(University of Western Brittany)</em>
        div
          //- img(src='./assets/claire.jpg')
          p Professor of Marine Biotechnology at the University of Bretagne Occidentale and Director of Biodimar, a technological core facility providing expert services in marine biomolecule extraction, purification and analysis. Professor Hellio is also a representative of the European Society for Marine Biotechnology.
  v-container(fluid class="base")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Workshops
        br
        h2 Duncan Yellowlees
        p We are so pleased to host a fantastic communication workshop this year with the talented speaker, <a href='http://duncanyellowlees.com'> Duncan Yellowlees</a>. Helping us turn our ideas into engaging and interesting presentations, Duncan will train us in the art of communicating logically and emotionally, person to person. Forget boring presentations even existed! It’s time we get inspired! This workshop will take place in the afternoon of Monday 6th May 2019 at 16:30. 
        
  v-container(fluid class="white")
    v-layout(fluid text-xs-left align-center justify-center row wrap)
      v-flex(xs11 sm10 md10 lg10)
        br
        h1 Sponsors

        v-container(text-xs-center)
          v-layout(row wrap)
            v-flex(xs12 sm12 md3 lg3)
              img(src='./assets/mcs.jpg' id='sponsor')
              a(href='https://www.mcsuk.org/')
                h2 Marine Conservation Society
            v-flex(xs12 sm12 md3 lg3)
              img(src='./assets/nifca.jpg' id='sponsor')
              a(href='https://www.nifca.gov.uk/')
                h2 Northumberland Inshore Fisheries Conservation Authority
            v-flex(xs12 sm12 md3 lg3)
              img(src='./assets/pouk.jpg' id='sponsor')
              a(href='https://planet-ocean.co.uk/wp/')
                h2 Planet Ocean UK
            v-flex(xs12 sm12 md3 lg3)
              img(src='./assets/snes.png' id='snes')
              a(href='https://www.ncl.ac.uk/nes/')
                h2 Newcastle University School of Natural and Environmental Sciences
           
</template>
 

<script>
export default {
  data() {
    return {
      // Change the name of the project
      title: "MBA PG Conference 2019",
      pages: [
        { name: "Home", link: "/" },
        { name: "About", link: "/about" },
        { name: "Keynotes and workshops", link: "/keynotes" },
        { name: "Program", link: "/program" },
        { name: "Sponsors", link: "/sponsors" },
        { name: "Contact", link: "/contact" }
      ]
    };
  }
};
</script>
 


